<template>
  <el-card shadow="never" class="portal-eChart-box">
    <CardHeader v-if="activeData.title" slot="header" :title="activeData.title"
      :card="activeData.card" />
    <div class="portal-box-body">
      <div class="Schedule-container app-container">
        <FullCalendar ref="calendar" :options="calendarOptions" />
        <!-- :dayNamesShort='dayNamesShort' :dayNames='dayNamesShort' axisFormat='HH:mm' timeFormat='HH:mm' -->
        <Form v-if="formVisible" ref="Form" @refreshDataList="refreshDataList" />
        <ScheduleView v-if='scheduleVisible' ref="schedule" @refreshDataList="refreshDataList" />
      </div>
    </div>
  </el-card>
</template>

<script>
import CardHeader from "../CardHeader"
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { List } from '@/api/onlineDev/schedule'
import Form from './Form'
import { mapGetters } from "vuex";
import ScheduleView from '@/components/ScheduleView'
// import listPlugin from '@fullcalendar/list'
import { calendar } from "./calendar"
export default {
  components: {
    CardHeader, FullCalendar, Form, ScheduleView
  },
  props: {
    title: { type: String, default: '' },
    option: { type: Object, default: () => { } },
    card: { type: Object, default: () => { } },
    activeData: { type: Object, default: () => { } },
    jnpfKey: { type: String, default: '' }
  },
  data() {
    return {
      calendarOptions: {},
      creatorUserId: '',
      calendarApi: '',
      formVisible: false,
      scheduleVisible: false,
    }
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  created() {
    this.init()
    this.creatorUserId = this.userInfo.userId

  },
  watch: {
    'activeData.firstDay'(val) {
      this.calendarOptions.firstDay = this.activeData.firstDay
    },
    'activeData.showLunarCalendar'(val) {
      this.init()
    },
    'activeData.defaultView'(val) {
      this.init()
    }
  },
  mounted() {
    this.$eventBus.$on('eChart' + this.activeData.i, () => {
      this.$refs.calendar && this.$refs.calendar.calendar.updateSize()
    })
    this.$nextTick(() => {
      this.$refs.calendar && this.$refs.calendar.calendar.updateSize()
    })
  },
  methods: {
    init() {
      const that = this
      this.calendarOptions = {
        // fullcalendar/daygridfullcalendar/timegrid
        plugins: [dayGridPlugin, interactionPlugin, timeGridPlugin],
        initialView: this.activeData.defaultView, // dayGridMonthtimeGridWeektimeGridDay
        firstDay: this.activeData.firstDay, // 01
        locale: 'zh-cn', // 
        eventColor: '#3BB2E3', // 
        themeSystem: 'bootstrap', // ()
        // initialDate: moment().format('YYYY-MM-DD'), // 
        aspectRatio: 1.65, // 
        // displayEventTime: false, // 
        // allDaySlot: false, // all-day 
        allDayText: '',//all-day 
        // eventLimit: true, // all-day slot
        headerToolbar: { // 
          left: 'prev next today',
          center: 'title',
          right: 'timeGridDay,timeGridWeek,dayGridMonth'
        },
        buttonText: {
          today: '',
          month: '',
          week: '',
          day: ''
        },
        slotLabelFormat: {
          hour: '2-digit',
          minute: '2-digit',
          meridiem: false,
          hour12: false // 24
        },
        // eventLimitNum: { // ()
        //   dayGrid: {
        //     eventLimit: 5
        //   },
        //   timeGrid: {
        //     eventLimit: 2 // adjust to 6 only for timeGridWeek/timeGridDay
        //   }
        // },
        // 
        dateClick: this.handleEventClick, // 
        eventClick: this.eventClick, // 
        events: [],
        // eventDblClick: this.eventDblClick, //  ()
        // eventClickDelete: this.eventClickDelete, //  ()
        // eventDrop: this.eventDrop, // 
        // eventResize: this.eventResize, // 
        // select: this.handleEventClick, // 
        // eventDidMount: this.eventDidMount, // 
        // loading: this.loadingEvent, // /
        // selectAllow: this.selectAllow, //truefalse
        // eventMouseEnter: this.eventMouseEnter, // 
        // allowContextMenu: false,
        editable: false, // 
        eventStartEditable: false, // EventtruefalseendTime
        eventDurationEditable: false, // Eventtruefalse
        selectable: false, // 
        selectMirror: false,
        selectMinDistance: 0, // 
        dayMaxEvents: false,
        weekends: true,
        navLinks: false, // 
        // selectHelper: false,
        datesSet: this.datesRender,
        slotEventOverlap: false, // true
        views: {
          //
          dayGridMonth: {
            displayEventTime: false,//
            dayMaxEventRows: 3,
            // eventMaxStack: 30,
            // moreLinkContent: "",
            moreLinkClick: "popover",
            dayCellContent(item) {
              let date = new Date(item.date);  //  1000
              let Y = date.getFullYear() + '-';
              let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
              let D = date.getDate()
              let date_ = Y + M + D
              let _date = date_.split('-')
              let _dateF = calendar.solar2lunar(_date[0], _date[1], _date[2])
              let myClass = ''
              if (_dateF.isToday) myClass = 'today-month'
              if (that.activeData.showLunarCalendar) {
                let IDayCn = _dateF.IDayCn
                if (IDayCn == '') IDayCn = _dateF.IMonthCn
                return { html: `<p class='calendar-right'><label class='${myClass}'>${_dateF.cDay}</label><span>${IDayCn}</span></p>` }
              }
              return { html: `<p class='calendar-right'><label class='${myClass}'>${_dateF.cDay}</label></p>` }
            },
          },
          //
          timeGridWeek: {
            // slotMinTime: "09:00",//
            // slotMaxTime: "20:00",//
            displayEventTime: false,//
            dayHeaderContent(item) {
              let date = new Date(item.date);  //  1000
              let Y = date.getFullYear() + '-';
              let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
              let D = date.getDate()
              let date_ = Y + M + D
              let _date = date_.split('-')
              let _dateF = calendar.solar2lunar(_date[0], _date[1], _date[2])
              let myClass = 'calender-week'
              if (_dateF.isToday) myClass = 'calender-week today-week'
              if (that.activeData.showLunarCalendar) {
                const htmlVal = `<div class='${myClass}'>${_dateF.cDay}</div><div class="list-week"><div>${_dateF.ncWeek.slice(2)}</div><div class='list-calendar'>${_dateF.IDayCn}</div></div></div>`
                return {
                  html: htmlVal
                }
              }
              return { html: `<div class='${myClass}'>${_dateF.cDay}</div><div class='list-week'><div >${_dateF.ncWeek.slice(2)}</div></div></div>` }
            }
          },
          timeGridDay: {
            // slotMinTime: "09:00",//
            // slotMaxTime: "20:00",//
            displayEventTime: false,//
            dayHeaderContent(item) {
              let date = new Date(item.date);  //  1000
              let Y = date.getFullYear() + '-';
              let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
              let D = date.getDate()
              let date_ = Y + M + D
              let _date = date_.split('-')
              let _dateF = calendar.solar2lunar(_date[0], _date[1], _date[2])
              let myClass = 'calender-week'
              if (_dateF.isToday) myClass = 'calender-week today-week'
              if (that.activeData.showLunarCalendar) {
                return {
                  html: `<div class='${myClass}'>${_dateF.cDay}</div><div class='list-week'><div>${_dateF.ncWeek.slice(2)}</div><div class='list-calendar'>${_dateF.IDayCn}</div></div></div>`
                }
              }
              return { html: `<div class='${myClass}'>${_dateF.cDay}</div><div class='list-week'><div >${_dateF.ncWeek.slice(2)}</div></div></div>` }

            }
          }
        },

      }
    },
    datesRender(calendar) {
      let view = calendar.view
      this.startTime = this.jnpf.toDate(view.activeStart, "yyyy-MM-dd HH:mm")
      this.endTime = this.jnpf.toDate(view.activeEnd, "yyyy-MM-dd HH:mm")
      this.initData()
    },
    handleEventClick(arg) {
      let startTime = this.jnpf.toDate(arg.date, "yyyy-MM-dd HH:00"),
        clickTime = this.jnpf.toDate(arg.date, "yyyyMMdd"),
        currTime = this.jnpf.toDate(new Date(), "yyyyMMdd");
      if (clickTime == currTime) {
        let thisDate = new Date();
        thisDate.setHours(thisDate.getHours() + 1);
        startTime = this.jnpf.toDate(thisDate, "yyyy-MM-dd HH:00");
      }
      this.addOrUpdateHandle('', startTime)
      this.addOrUpdateHandle('', new Date(startTime).getTime())
    },
    eventClick(data) {
      this.addOrUpdateHandle(data.event.id, '', data.event.extendedProps.creatorUserId)
    },
    initData() {
      let query = {
        startTime: this.startTime,
        endTime: this.endTime,
      }
      List(query).then(res => {
        this.calendarOptions.events = res.data.list.map(o => {
          return {
            id: o.id,
            title: o.title,
            start: o.startDay,
            end: o.endDay,
            color: o.color,
            editable: false,
            allDay: o.allDay,
            creatorUserId: o.creatorUserId
          }
        })
      })
    },
    //  / 
    addOrUpdateHandle(id, startTime, creatorUserId) {
      if (id && (creatorUserId != this.creatorUserId)) {
        this.scheduleVisible = true
        this.$nextTick(() => {
          this.$refs.schedule.init(id, 1, '')
        })
        return
      }
      this.formVisible = true
      this.$nextTick(() => {
        this.$refs.Form.init(id, startTime)
      })
    },
    refreshDataList() {
      this.formVisible = false
      this.scheduleVisible = false
      this.initData()
    }
  }
}
</script>

<style lang="scss" scoped>
.portal-box-body {
  height: 100%;
  overflow: auto;
}
</style>